"use client";
import { BookingForm } from "@/features/booking-form/schemas";
import { InputsContacts } from "@/features/booking-form/ui/inputs-contacts";
import { useStep } from "@/shared/ui/step-context";
import { Control } from "react-hook-form";

export interface BookingSteperProps {
  control: Control<BookingForm>;
}
export const BookingSteper = ({ control }: BookingSteperProps) => {
  const { step } = useStep();
  const handlerContactPageNext = () => {
    const values = getValues();
    const result = contactSchema.safeParse(values);

    if (!result.success) {
      const errors = result.error.flatten().fieldErrors;

      Object.entries(errors).forEach(([key, [message]]) => {
        if (message) {
          setError(key as keyof BookingForm, {
            type: "manual",
            message,
          });
        }
      });

      return;
    }

    clearErrors();
    nextStep();
  };
  switch (step) {
    case 1:
      return (
        <div>
          <InputsContacts control={control} />
          <StepButtonBar
            childrenNextButton={
              <StepButton variant="next" onClick={handlerContactPageNext} />
            }
          />
        </div>
      );
    case 2:
      return <div>Step 2 content</div>;
    case 3:
      return <div>Step 3 content</div>;
    default:
      return <div>Unknown step</div>;
  }
};
